body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}


.game {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
button.square {
  width: 100px;
  font-size: 70px;
  height: 100px;
}
.title {
  font-size: 24px;
}

.game-info {
  margin-left: 20px;
}
.game-board {
  max-width: 480px;
  max-height: 480px;
}
.board.won-row0 .row0 .symbol,
.board.won-row1 .row1 .symbol,
.board.won-row2 .row2 .symbol,
.board.won-column0 .symbol.column0,
.board.won-column1 .symbol.column1,
.board.won-column2 .symbol.column2,
.board.won-leftSlant .row0 .symbol.column0,
.board.won-leftSlant .row1 .symbol.column1,
.board.won-leftSlant .row2 .symbol.column2,
.board.won-rightSlant .row0 .symbol.column2,
.board.won-rightSlant .row1 .symbol.column1,
.board.won-rightSlant .row2 .symbol.column0 {
  background-color: mediumseagreen;
}

.board.draw .symbol {
  background-color: palevioletred;
}

.row {
  display: -webkit-flex;
  display: flex;
}


@-webkit-keyframes symbol-appear {
  from {
    -webkit-transform: scale(1.9);
            transform: scale(1.9);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}


@keyframes symbol-appear {
  from {
    -webkit-transform: scale(1.9);
            transform: scale(1.9);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

svg {
  -webkit-animation-name: symbol-appear;
          animation-name: symbol-appear;
  -webkit-animation-duration: .15s;
          animation-duration: .15s;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

* {
  box-sizing: border-box;
}

